<template>
  <section>
    <section v-if="list.length > 0">
      <Table border :columns="columns" :data="list">
        <template slot-scope="{ row }" slot="name">
          <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{ row, index }" slot="action">
          <Button type="primary" size="small" @click="reply(row.id)" style="margin-right: 5px"></Button>
          <Button type="error" size="small" @click="destroy(row.id)"></Button>
        </template>
      </Table>

      <section class="page">
        <Page :total="page.total" :page-size="page.per_page" :current="page.current_page" show-total
              @on-change="handlePage"></Page>
      </section>

    </section>
  </section>
</template>

<script>
  import merge from 'webpack-merge'
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "list",
    data() {
      return {
        list: [],
        page: {},
        currentPage: 1,
        columns: [
          {
            title: 'ID',
            key: 'id',
            width: 80,
            align: "center"
          },
          {
            title: '',
            key: 'nickname'
          },
          {
            title: '',
            key: 'email'
          },
          {
            title: '',
            key: 'content'
          },
          {
            title: '',
            key: 'target_type',
            width: 100,
            align: "center",
            render: (h, params) => {
              return h('div', [
                h('span', params.row.target_type === 'article' ? '' : '')
              ]);
            }
          },
          {
            title: 'Action',
            slot: 'action',
            width: 200,
            align: 'center'
          }
        ]
      }
    },
    created() {
      this._getCommentsList();
    },
    methods: {
      ...mapActions({
        getCommentsList: 'comment/getCommentsList',
        destroyComments: 'comment/destroyComments'
      }),
      // 
      async _getCommentsList() {
        const res = await this.getCommentsList({
          page: this.currentPage
        });

        this.list = res.data.data.data;
        this.page = res.data.data.meta;
      },
      // 
      handlePage(page) {
        this.$router.replace({
          query: merge(this.$route.query, {
            page
          })
        });
        this.currentPage = page;
        this._getCommentsList();
      },
      reply(id) {
        this.$router.push('/reply/' + id)
      },
      // 
      destroy(id) {
        this.$Modal.confirm({
          title: '',
          content: '<p></p>',
          loading: true,
          onOk: async () => {
            try {
              await this.destroyComments(id);
              this.$Message.success('');

              this._getCommentsList();

            } catch (e) {
              this.$Message.error(e);

            } finally {
              this.$Modal.remove();
            }

          },
          onCancel: () => {
            this.$Message.warning('');
          }
        });
      }
    }
  }
</script>

<style scoped>
  .page {
    padding: 32px 0;
    text-align: center;
  }
</style>



